/* src/components/Contact.css */
.contact {
  padding: 4rem 2rem;
  background: linear-gradient(45deg, #868484 0%, #817e7f 100%);
  color: white;
  text-align: center;
}

.contact h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

.contact-info {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}

.contact-item {
  display: flex;
  align-items: center;
}

.contact-item span {
  margin-left: 1rem;
}

.social-media a {
  color: white;
  font-size: 1.5rem;
  margin-right: 1rem;
  transition: color 0.3s;
}

.social-media a:hover {
  color: #f9f9f9;
}

.contact form {
  max-width: 600px;
  margin: 0 auto;
}

.contact input,
.contact textarea {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: none;
  border-radius: 4px;
}

.contact button {
  padding: 0.75rem 2rem;
  background-color: #39342f;
  border: none;
  border-radius: 4px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.contact button:hover {
  background-color: #110e10;
}
