/* src/components/Home.css */
.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(45deg, #360033 0%, #0b8793 100%);
    color: white;
    text-align: center;
  }
  
  .home h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  .home p {
    font-size: 1.5rem;
    margin: 0;
  }
  