/* src/components/Projects.css */
.projects {
    padding: 4rem 2rem;
    background-color: #fff;
    color: #333;
    text-align: center;
  }
  
  .projects h2 {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
  }
  
  .project-list {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
  }
  
  .project-item {
    background-color: #f9f9f9;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: calc(33.333% - 2rem);
    min-width: 250px;
  }
  
  .project-item h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .project-item p {
    font-size: 1rem;
    color: #666;
  }
  